.home {
    height: 100vh;
    width: 100vw;
    // display: block;


    .landing {
        height: 100vh;
        width: 100vw;
        background-color: #f6c9f5;
        // background-image: radial-gradient(circle,
        //         #f6c9f5 0%,
        //         #d0e7ff 50%,
        //         #cac9ff 100%);

        // background-image: linear-gradient(64.5deg, rgba(245, 116, 185, 1) 14.7%, rgba(89, 97, 223, 1) 88.7%);
        background-image: linear-gradient(219.3deg, rgba(149, 0, 199, 1) 4.7%, rgba(130, 0, 226, 1) 22.9%, rgba(95, 0, 199, 1) 43.2%, rgba(70, 0, 199, 1) 63.9%, rgba(5, 0, 199, 1) 85.1%, rgba(0, 95, 199, 1) 106.8%);

        .landingTextBox2 {
            padding: 70px 0px 0px 0px;
            // height: 300px;
            // width: 300px;

            span {
                // padding-left: 10px;
                font-size: 50px;
                padding-left: 50px;
                margin-top: -15px;
            }


        }

        .spaceIcon {
            position: absolute;
            top: 35%;
            left: 50%;
            transform: rotate(15deg);

            img {
                width: 800px;
                opacity: 0.2;
            }
        }

        .downArrow {
            font-size: 50px;
            position: absolute;
            top: 90%;
            left: 5%;

            a {
                // text-decoration: none;
                color: black;
            }
        }

        .landingTextBox {
            height: 70%;
            width: 50%;
            padding: 200px 0% 0% 200px;

            .landingText {
                font-size: 60px;
            }


            .slidingVertical span {
                animation: topToBottom 12.5s linear infinite 0s;
                -ms-animation: topToBottom 12.5s linear infinite 0s;
                -webkit-animation: topToBottom 12.5s linear infinite 0s;
                color: white;
                opacity: 0;
                overflow: hidden;
                position: absolute;
            }

            .slidingVertical span:nth-child(2) {
                animation-delay: 2.5s;
                -ms-animation-delay: 2.5s;
                -webkit-animation-delay: 2.5s;
            }

            .slidingVertical span:nth-child(3) {
                animation-delay: 5s;
                -ms-animation-delay: 5s;
                -webkit-animation-delay: 5s;
            }

            .slidingVertical span:nth-child(4) {
                animation-delay: 7.5s;
                -ms-animation-delay: 7.5s;
                -webkit-animation-delay: 7.5s;
            }

            .slidingVertical span:nth-child(5) {
                animation-delay: 10s;
                -ms-animation-delay: 10s;
                -webkit-animation-delay: 10s;
            }

            @keyframes topToBottom {
                0% {
                    opacity: 0;
                }

                5% {
                    opacity: 0;
                    -moz-transform: translateY(-50px);
                }

                10% {
                    opacity: 1;
                    -moz-transform: translateY(0px);
                }

                25% {
                    opacity: 1;
                    -moz-transform: translateY(0px);
                }

                30% {
                    opacity: 0;
                    -moz-transform: translateY(50px);
                }

                80% {
                    opacity: 0;
                }

                100% {
                    opacity: 0;
                }
            }

            @keyframes topToBottom {
                0% {
                    opacity: 0;
                }

                5% {
                    opacity: 0;
                    -webkit-transform: translateY(-50px);
                }

                10% {
                    opacity: 1;
                    -webkit-transform: translateY(0px);
                }

                25% {
                    opacity: 1;
                    -webkit-transform: translateY(0px);
                }

                30% {
                    opacity: 0;
                    -webkit-transform: translateY(50px);
                }

                80% {
                    opacity: 0;
                }

                100% {
                    opacity: 0;
                }
            }

            @-ms-keyframes topToBottom {
                0% {
                    opacity: 0;
                }

                5% {
                    opacity: 0;
                    -ms-transform: translateY(-50px);
                }

                10% {
                    opacity: 1;
                    -ms-transform: translateY(0px);
                }

                25% {
                    opacity: 1;
                    -ms-transform: translateY(0px);
                }

                30% {
                    opacity: 0;
                    -ms-transform: translateY(50px);
                }

                80% {
                    opacity: 0;
                }

                100% {
                    opacity: 0;
                }
            }

        }


    }
}

@media screen and (max-width:960px) {
    .home {
        display: grid;
        grid-template-columns: auto;

        .landing {
            .landingTextBox {
                // height: 400px;
                // width: 400px;
                padding: 50px 0px 0px 100px;

                .landingText {
                    font-size: 45px;
                }

            }





        }

    }


}